import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo from '../Images/logo.png';
import us from '../Images/US.png';
import 'font-awesome/css/font-awesome.min.css';
import {react,useEffect,useState} from'react';

function BasicExample() {
  


  return (
    <Navbar expand="lg" className="bg-body-tertiary" style={{borderRadius:50}}>
      <Container className="px-4">
        <Navbar.Brand href="/"><a href='/'><img src={logo}  style={{width:60}}/></a></Navbar.Brand>
    
        <Navbar.Toggle className="shadow-none border-0 p-3" id="newtoggle"><i className="fa fa-bars py-1 px-2 " style={{fontSize:22,color:'#491d4d',border:'2px solid #491d4d',borderRadius:5}}></i></Navbar.Toggle>

        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className=" me-auto mb-2 mb-lg-0 mx-lg-auto">
            <Nav.Link href="/" className="me-3 mx-3 text-black">Home</Nav.Link>
            <Nav.Link href="#AboutUs" className="me-3 mx-3 text-black">About Us</Nav.Link>
            <Nav.Link href="#Services" className="me-3 mx-3 text-black">Services</Nav.Link>
            <Nav.Link href="#ContactUS" className="me-3 mx-3 text-black">Contact Us</Nav.Link>         
          </Nav>
          <img className='ms-sm-3' src={us} style={{width:37}}/>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default BasicExample;